import format from 'date-fns/format';
import intervalToDuration from 'date-fns/intervalToDuration'

const MONTHES = [`.`, `.`, ` .`, `.`, ``, `.`, `.`, `.`, `.`, `.`, `.`, `.`];
const DAYS_OF_WEEK = [``, ``, ``, ``, ``, ``, ``];

/**
 *        HH:mm
 * @param {Number} date -   unix 
 * @return {String}     HH:mm
 */
export const formatHoursMinutes = (date) => {
  return format(date, `HH:mm`);
};

/**
 *        D MMM ddd   
 * @param {Number} date -   unix 
 * @return {String}     D MMM ddd   
 */
export const formatDayMonth = (date) => {
  return `${format(date, `d`)} ${MONTHES[format(date, `M`)]} ${DAYS_OF_WEEK[format(date, `i`)]}`;
};

export const getDistanceFull = (departure, arrival) => {
  const duration = intervalToDuration({
    start: departure,
    end: arrival
  });

  return `${duration.days > 0 ? duration.days + ` ` : ``} ${duration.hours > 0 ? duration.hours + ` ` : ``} ${duration.minutes} `;
  //     
};
